.reviews {
    margin: 90px auto 0 auto;
    height: 570px;
    width: 100%;
    background-image: url(../img/bg-testimonials.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.user-info {
    margin-top: 102px;
    opacity: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &.active-user {
        opacity: 1;
    }
}

.users {
    max-width: 520px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.user-pic {
    border: 2px solid #fff;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background-image: url(../img/user.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.user-name {
    margin-top: 29.3px;
    font: 16px / normal $fnt_Bold;
    line-height: 1.63;
    color: #ffffff;
}

.comments {
    position: relative;
    top: 0;
    margin-top: 30px;
    width: 1110px;
    height: 210px;
    background: rgba(255, 255, 255, 0.1);

    &::before {
        content: "";
        position: absolute;
        top: -16px;
        left: 50%;
        right: 50%;
        border-left: 16px solid transparent;
        border-right: 16px solid transparent;
        border-bottom: 16px solid rgba(255, 255, 255, 0.1);
    }
}

.message {
    padding-top: 50px;
    display: block;
    text-align: center;
    line-height: 1.73 !important;
    color: #fff;
    font: 16px / normal $fnt_Light;
    max-width: 1008px;
    margin: 0 auto;
}

.star {
    height: 20px;
    width: 20px;
    color: #ff7373;
}
