.footer {
    padding-top: 50px;
    background-color: #623232;
}

.footer-nav {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
}

.contact-container {
    display: flex;
}

.contact-img {
    margin: 0 20px 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ff7373;
    width: 55px;
    height: 55px;
}

.contact-img:last-child {
    margin: 0;
}

.phone-icon {
    width: 20px;
    height: 20px;
    background-image: url(../img/phone2.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.mail-icon {
    width: 20px;
    height: 14px;
    background-image: url(../img/mail2.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.geo-icon {
    width: 20px;
    height: 20px;
    background-image: url(../img/geo.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.active-title {
    font: 16px / normal $fnt_Bold;
    color: #ff7373;
    text-transform: uppercase;
}

.number {
    line-height: 0.93 !important;
    margin: 10px 0 0 0;

    &:hover {
        color: #ff7373;
        text-decoration: underline;
        cursor: pointer;
    }
}

.number,
.second-text {
    font: 16px / normal $fnt_Light;
    color: #ffffff;
}

.article-contact {
    max-width: 295px;
    margin-right: 7.1px;
}

.article-contact_container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-height: 40px;
    max-width: 212px;
    width: 100vw;
}

.nav__item {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    &:last-child {
        margin-right: 28px;
    }
}

.second-text {
    margin-top: 16.8px;
    width: fit-content;
    height: fit-content;

    &:hover {
        color: #ff7373;
        text-decoration: underline;
    }
}

.footer-bottom,
.social {
    display: flex;
    justify-content: space-between;
}

.footer-bottom {
    margin-top: 100px;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    padding: 22.2px 0;

    & > .second-text {
        margin-top: 3.5px;
        margin-right: 14.2px;
    }
}

.social {
    max-width: 173.6px;
    width: 100vw;
    align-items: center;
}

#twitter {
    width: 27px;
    height: 22px;
    background-image: url(../img/twitter.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#fb {
    width: 13px;
    height: 27px;
    background-image: url(../img/fb.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#pinterest {
    width: 27px;
    height: 27px;
    background-image: url(../img/pint.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#yt {
    width: 27px;
    height: 20px;
    background-image: url(../img/yt.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

#twitter,
#fb,
#pinterest,
#yt:hover {
    cursor: pointer;
}
