.connection {
    margin-top: 100px;
    height: 200px;
    background: #ff7373;
}

.connection-container {
    display: flex;
    justify-content: space-between;
    padding-top: 73px;

    & > div {
        display: flex;
    }
}

.email {
    margin: 6px 30px 0 0;
    width: 60px;
    height: 42px;
    background-image: url(../img/envelope.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.connection-title {
    margin-bottom: 18px;
    text-transform: uppercase;
    font: 30px / normal $fnt_Bold;
}

.connection-title,
.connection-description {
    line-height: 0.53 !important;
    color: #ffffff;
}

.connection-description {
    font: 16px / normal $fnt_Light;
}
